export const mint = async () => {

    let cost = 50000000000000000;
    let gasLimit = 285000;
    let totalCostWei = String(cost * 1);
    let totalGasLimit = String(gasLimit * 1);

    console.log("address: ", currentConnectWallet);
    let proof = await getMerkleProof(currentConnectWallet);
    console.log("merkle proof", proof);

    if (proof.length == 0) {
        return
    }

    const receipt = await littlePrettyContract.methods
    .mint(1, proof)
    .send({
        gasLimit: totalGasLimit,
        to: contractAddress,
        from: currentConnectWallet,
        value: totalCostWei
    })

    console.log("receipt", receipt);
};


const getMerkleProof = async (address) => {
    let index = allower.indexOf(address.toLowerCase());
    if (index < 0) {
        console.log("Addr " + address + "is not in whitelist");
        return ""
    }
    let hexProof = merkleTree.getHexProof(leafNodes[index]);
    return hexProof;
};


export const buildMerkleTree = async () => {
    merkleTree = new MerkleTree(leafNodes, keccak256, {
        sortPairs: true
    });

    merkleRoot = merkleTree.getRoot();
};