import React from "react";
import { useEffect, useState } from "react";
import {
  connectWallet,
  loadTotalSupply,
  getCurrentWalletConnected,
  buildMerkleTree,
  mint,
} from "./util/interact.js";

const LittlePretty = () => {
  //state variables
  const [walletAddress, setWallet] = useState("");
  const [, setStatus] = useState("");
  const [, setMessage] = useState("No connection to the network."); //default message
  // const [newMessage, setNewMessage] = useState("");
  // const [newTotalSupply, setNewTotalSupply] = useState("");

  //called only once
  useEffect(() => {
    async function bootstrap() {
      await buildMerkleTree();
      const totalSupply = await loadTotalSupply();
      setMessage(totalSupply);
      const {address} = await getCurrentWalletConnected();
      setWallet(address);
    };
    bootstrap();
  }, []);

  const mintPresses = async () => {
    await mint();
  }

  const connectWalletPressed = async () => {
    const walletResponse = await connectWallet();
    setStatus(walletResponse.status);
    setWallet(walletResponse.address);
  };

  //the UI of our component
  return (
    <div id="container">
      {/* <img id="logo" src={alchemylogo}></img> */}
      <button id="walletButton" onClick={connectWalletPressed}>
        {walletAddress.length > 0 ? (
          "Connected: " +
          String(walletAddress).substring(0, 6) +
          "..." +
          String(walletAddress).substring(38)
        ) : (
          <span>Connect Wallet</span>
        )}
      </button>

      <button id="mintButton" onClick={mintPresses}>
          <span>Mint</span>
      </button>
    </div>
  );
};

export default LittlePretty;
